import React from 'react';
import './tour.css';

function Tour({id, image, info, name, price, deleteItem}) {
    return (
        <div key={id} className="tour-carts">
            <div className="cart">
                <img src={image} alt={name} />
                <div className="info">
                    <h1>{name}</h1>
                    <h3>${price}</h3>
                </div>
                <p className="text">{info}</p>
                <div className="tour-btn">
                    <button onClick={() => deleteItem(id)}>Not Intrested</button>
                </div>
            </div>
        </div>
    )
}

export default Tour