import React from 'react';
import './home.css';
import Header from '../header/Header';
import Tours from '../tours/Tours';

function Home({tour, deleteItem}) {
    return (
        <div className="header">
            <div className="main-header">
                <Header />
                <div className="title">
                    <h1>Welcome to Star Hotel Booking Website</h1>
                    <div className="btn-groups">
                        <a href="#">Get Started</a>
                    </div>
                </div>
            </div>
            <Tours  tour={tour} deleteItem={deleteItem}/>
        </div>
    )
}

export default Home
