import React, { useEffect, useState } from "react";
import Home from "./components/home/Home";
import Loading from "./components/loading/Loading";
import Review from "./components/review/Review";

const url = "https://course-api.com/react-tours-project";

function App() {
  const [tour, setTour] = useState([]);
  const [loading, setLoading] = useState(false);

  const deleteItem = (id) => {
    const newFilter = tour.filter((item) => item.id !== id);
    setTour(newFilter);
  };

  const tourFetch = async () => {
    setLoading(true);
    try {
      const response = await fetch(url);
      const tour = await response.json();
      setLoading(false);
      setTour(tour);
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
  };
  useEffect(() => {
    tourFetch();
  }, []);

  if (loading) {
    return (
      <main>
        <Loading />
      </main>
    );
  }
  if (tour.length === 0) {
    return (
      <section className="map">
        <div className="tour-title">
          <h1>You have deleted all Lists</h1>
          <div className="btns">
            <button className="btn" onClick={tourFetch}>Refresh</button>
          </div>
        </div>
      </section>
    );
  }
  return (
    <div className="container">
      <Home tour={tour} deleteItem={deleteItem} />
      <Review />
    </div>
  );
}

export default App;
