import React from 'react'
import Tour from '../tour/Tour'
import "./tours.css";

function Tours({tour, deleteItem}) {
    return (
        <section className="map">
            <div className="tour-title">
                <h1>Booking List</h1>
                <div className="underlined"></div>
            </div>
            {tour.map((person) => {
                return <Tour key={person.id} {...person} deleteItem={deleteItem}></Tour>
            })}
        </section>
    )
}

export default Tours
