import React, { useState } from 'react';
import "./review.css";
import data from '../../data';
import { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io';

function Review() {

    const [index, setIndex] = useState(0);
    const {image, job, name, text, id} = data[index];

    const errorSolve = (number) => {
        if(number > data.length -1){
            return 0
        }if(number < 0) {
            return data.length -1
        }else{
            return number
        }
    }

    const clickMeForward = () => {
        setIndex((index) => {
            const newClick = index + 1;
            return errorSolve(newClick);
        })
    }

    const clickMeBackward = () => {
        setIndex((index) => {
            const newClick = index - 1;
            return errorSolve(newClick);
        })
    }

    const randomClick =() => {
        const random = Math.floor(Math.random() * data.length);
        if(random === index){
            random = index + 1
        }
        setIndex(errorSolve(random));
    }


    return (
        <div key={id} className="reviews">
            <div className="review-title"><h1>Testimonials</h1></div>
            <div className="review">
                <img src={image} alt={name} />
                <h3>{name}</h3>
                <h6>{job}</h6>
                <p>{text}</p>
            <div className="group">
                <button onClick={clickMeBackward} className="icon"><IoIosArrowBack/></button>
                <button onClick={clickMeForward} className="icon"><IoIosArrowForward/></button>
                <div className="btn">
                    <button onClick={randomClick}>Random Click</button>
                </div>
            </div>
            </div>
        </div>
    )
}

export default Review
